.c-patient-search {
  display: block;
  height: 52px;
  display: flex;
  align-items: center;
}

.c-patient-search__query {
  border: none;
  margin-left: $global-spacing-unit-small;
  @include font-size(18px);
}

.c-patient-search__icon {
  color: #ccc;
}

.c-patient-search__input-wrapper {
  display: flex;
  align-items: center;
}
