.c-tt__wrapper {
  height: inherit;
  display: flex;
  flex-direction: column;
}

.c-tt__content {
  display: flex;
  flex-direction: column;
}

.c-tt__header {
  padding: $global-spacing-unit - 4 $global-spacing-unit;
}

.c-tt__header__wrapper {
  display: flex;
  align-items: center;
}

.c-tt__header-heading {
  margin: 0;
}

.c-tt__header__user-wrapper {
  flex: auto;
}

.c-tt__ruler {
  margin: 0;
}

.c-tt__header__user {
  margin-left: $global-spacing-unit-huge;
}

.c-tt__header-button {
  @include font-size(16px);
}

.c-tt__filters {
  display: flex;
  justify-content: center;
}

.c-tt__filters-form {
  border: none;
}

.c-tt__filters-form {
  display: flex;
  flex-direction: column;
}

.c-tt__filters-form form {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.c-tt__filters-form varian-select {
  display: block;
  overflow-x: auto;
}

.c-tt__filters__heading {
  font-weight: bold;
  margin-left: $global-spacing-unit-tiny;
}

.c-tt__list {
  margin: 0;
  padding: 0;
}
.c-tt__list-item {
  margin: 0;
  padding: 0;
  list-style: none;
}
.c-tt__list-item label {
  padding: $global-spacing-unit-small $global-spacing-unit-tiny;
}

.c-tt-button {
  text-align: right;
}

.c-tt-templates-submit {
  position: absolute;
  bottom: $global-spacing-unit;
  right: $global-spacing-unit;
}

.c-tt__templates-set[disabled], .c-tt__filters__heading[disabled], .c-tt__filters-form[disabled] {
  visibility: hidden;
}

.c-tt__footer {
  position: absolute;
  text-align: right;
  background-color: #DFE5EB;
  height: 4rem;
  line-height: 4rem;
  padding: 0 $global-spacing-unit;
  bottom: 0;
  right: 0;
  width: 100%;
}

.c-tt__dummy-footer {
  height: 4rem;
}
