.c-body__parts > input:checked + .c-body__parts-sub {
  visibility: visible;
}

.c-body__parts .c-body__parts-sub {
  visibility: hidden;
}

.c-body {
  position: relative;
  font-size: 10px;
}

  .c-body__whole-body {
  }
    .c-body__whole-body > img {
      position: absolute;
      top: 0;
      left: 0;
      width: 38.7em;
      visibility: hidden;
    }

    .c-body__whole-body input:checked+img {
      visibility: visible;
    }

  .c-body__part {
    position: absolute;
    opacity: 0;
    cursor: pointer;
  }

  .c-varian-select--selected .c-body__part img, .c-body__part:hover {
    opacity: 1;
  }

  .c-body__radio-button {
    visibility: hidden;
  }

  .c-body__body {}
    .c-body__body > img {
      width: 38.7em;
    }

  .c-body__head {
    top: 0;
    left: 15.5em;
  }
    .c-body__head > img {
      width: 7.8em;
    }

  .c-body__chest {
    top: 10.7em;
    left: 12.1em;
  }
    .c-body__chest > img {
      width: 14.2em;
    }

  .c-body__arms {
    top: 13em;
    left: 0;
  }
    .c-body__arms > img {
      width: 39em;
    }

  .c-body__abdomen {
    top: 23.9em;
    left: 13.3em;
  }
    .c-body__abdomen img {
      width: 12.2em;
    }

  .c-body__pelvis {
    top: 31.7em;
    left: 12.6em;
  }
    .c-body__pelvis > img {
      width: 13.5em;
    }

  .c-body__legs {
    top: 38em;
    left: 10.3em;
  }
    .c-body__legs > img {
      width: 18.1em;
    }
