.c-toolbar {
  display: flex;
  align-items: center;
  background-color: #2F4A59;
  color: #fff;
  height: 64px;
}

.c-toolbar__patient {
  margin-left: $global-spacing-unit;
  border-right: 1px solid #fff;
  padding-right: $global-spacing-unit;
  height: 100%;
  display: flex;
  align-items: center;
}

.c-toolbar__title {
  flex: auto;
  margin-left: $global-spacing-unit;
}

.c-toolbar__logout {
  margin-left: $global-spacing-unit-small;
}

.c-toolbar__logout-button {
  color: inherit;
}

.c-toolbar__actions {
  display: flex;
  align-items: center;
  height: 100%;
  border-left: 1px solid #fff;
  padding: $global-spacing-unit-small $global-spacing-unit;
}

.c-toolbar__rdm-icon-container {
  padding: $global-spacing-unit-small $global-spacing-unit;
}
