.c-patient-summary {
  display: flex;
}

.c-patient-summary__image {
  background-color: #ccc;
  background-repeat:no-repeat;
  background-position: center center;
  border-radius: 999em;
  width: 50px;
  height: 50px;
  font-size: 24px;
  vertical-align: middle;
  line-height: 50px;
  text-align: center;
}

.c-patient-summary__title-wrapper {
  margin-left: $global-spacing-unit-small;
  width: 75%;
}

.c-patient-summary__title {
  @include font-size(16px);
}

.c-patient-summary__subtitle {
  @include font-size(12px);
  width: 80%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
