.c-intent {
  @include font-size(16px);
  display: flex;
  flex-direction: column;
}

.c-intent__title {
  display: flex;
  padding: $global-spacing-unit-small $global-spacing-unit $global-spacing-unit;
}

.c-intent__template {
  margin-left: $global-spacing-unit-large;
}

.c-intent__phases {
  margin-left: $global-spacing-unit;
  margin-bottom: $global-spacing-unit;
}

  .c-intent__phases-container {
    width: 100%;
    overflow-y: scroll;
    white-space: nowrap;
    font-size: 0;
    display: flex;
  }

  .c-intent__phases-item {
    display: flex;
    flex: none; // prevent shrinking
    margin-left: $global-spacing-unit;
    @include font-size($global-font-size);
  }
  .c-intent__phases-item:first-child {
    margin-left: 0;
  }

  .c-intent__phases-targets {
    margin: 0;
    padding: 0;
  }

  .c-intent__phases-target {
    display: flex;
    justify-content: space-between;
    list-style: none;
  }

  .c-intent__phases-target-item {
    flex: 1;
  }

  .c-intent__phases-target-item:last-child {
    flex: none;
  }

.c-intent__card {
  background-color: #F1F3F5;
  padding: $global-spacing-unit-small;
  width: 100%;
}

  .c-intent__card__header {
    display: flex;
    margin-bottom: $global-spacing-unit-small;
  }

  .c-intent__card__ruler {
    margin-bottom: $global-spacing-unit-small;
  }

  .c-intent__card__title {
    flex: auto
  }

  .c-intent__card__targets {
    margin-left: $global-spacing-unit;
  }

.c-intent__notes {
  padding-left: $global-spacing-unit;
  padding-right: $global-spacing-unit;
}

  .c-intent__notes-heading {
    margin-bottom: $global-spacing-unit-small;
  }

  .c-intent__notes-text {
    padding: $global-spacing-unit-tiny $global-spacing-unit-small;
    border: 1px solid rgba(0, 0, 0, 0.10);
  }


.c-intent__lists-container {
  display: flex;
  flex-direction: column;
}
  .c-intent__lists-container > .o-layout {
    display: flex;
  }
  .c-intent__lists-container > .o-layout > .o-layout__item {
    display: flex;
    flex-direction: column;
  }

.c-intent__list {
  display: flex;
  flex-direction: column;
}

  .c-intent__list__heading {
    margin: 0;
  }

  .c-intent__list__item-color {
    height: 16px;
    width: 16px;
  }

  .c-intent__list__item-title {
    margin-left: $global-spacing-unit-tiny;
  }

.c-expandable-item__actions-dummy {
  margin-left: $global-spacing-unit;
  width: 24px;
  height: 24px;
}

.c-intent__list-icon-close {
  display: none;
}
.c-varian-expandable-item__button--active .c-intent__list-icon-open {
  display: none;
}
.c-varian-expandable-item__button--active .c-intent__list-icon-close {
  display: block;
}

.c-intent__goal, .c-intent__priority {
  @include font-size(12px);
}

  .c-intent__goal-label {
    display: inline-block;
    text-align: right;
    width: 50px;
    color: #757575;
  }

.c-intent__organs {

}

  .c-intent__organs-content {
    background-color: #F1F3F5;
    border: 1px solid rgba(66,84,99,0.20);
  }

  .c-intent__organs-item {
    display: flex;
    align-items: center;
    background-color: #fff;
    box-shadow: 0.5px 0.5px 0.5px 0px #ccc;
    margin-bottom: $global-spacing-unit-tiny;
    padding: $global-spacing-unit-tiny $global-spacing-unit-small;
  }

  .c-intent__organs-item-name {
    margin-left: $global-spacing-unit-tiny;
  }
