.c-expandable-item {
  background-color: #fff;
  box-shadow: 0.5px 0.5px 0.5px 0px #ccc;
  margin-bottom: $global-spacing-unit-small;
  padding: 0 $global-spacing-unit-small;
}

  .c-expandable-item:last-child {
    margin-bottom: 0;
  }

  .c-expandable-item--raw {
    padding: 0;
    background: none;
    border: none;
    box-shadow: none;
  }

  .c-expandable-item__header {
    display: flex;
    align-items: center;
    padding: $global-spacing-unit-tiny 0;
  }

  .c-expandable-item__title {
    flex: auto;
  }

  .c-expandable-item__actions {
    margin-left: $global-spacing-unit;
  }

  .c-expandable-item__ruler {
    margin-bottom: 0;
  }

  .c-expandable-item__details {
    margin-bottom: 0;
  }

  .c-expandable-item__detail {
    padding-top: $global-spacing-unit-tiny;
    padding-bottom: $global-spacing-unit-tiny;
  }
