.c-patient-card {
  padding: $global-spacing-unit-small;
  background-color: #fff;
  box-shadow: 0.5px 0.5px 0.5px 0px #ccc;
}

.c-patient-card__header {
  margin-bottom: $global-spacing-unit-small;
}

.c-patient-card__title-wrapper {
  width: 75%;
}

.c-patient-card__ruler {
  margin: 0;
  margin-bottom: $global-spacing-unit-tiny;
}

.c-patient-card__content {
  margin-bottom: $global-spacing-unit;
}

.c-patient-card__footer {
  text-align: right;
}

.c-patient-card__button a {
  text-decoration: none;
  color: inherit;
}

.c-patient-card__button-text {
  display: inline-block;
  margin-left: $global-spacing-unit-tiny;
}

.c-patient-card__intent-list {
  margin: 0;
  height: 100px;
  overflow-x: auto;
}
.c-patient-card__intent-item {
  list-style: none;
  color: #2F75B3;
}
